import React, { useEffect, useState } from "react";
import { UserWrapper } from "../css/usermanage-style";
import getUserList from "../api/userManageFetch";

const UserManage = () => {
  const [userList, setUserList] = useState([])
  const [isOpen, setIsOpen] = useState(false)
  const [clientList, setClientList] = useState()

  //    
  const AllUserListData = async () => {
    try {
      const data = await getUserList()
      console.log(" ",data)
      setUserList(data)
    }catch(err){
      console.log(err)
    }
  }

  const handleClickShow = () => {
    setIsOpen(true)
  }

  //    
  useEffect(() => {
    AllUserListData()
  },[])

  return (
    <UserWrapper>
      <div className="user_wrapper">
        <div className="user_inner">
          <h1> </h1>
          <div className="user_list_top">
            <ul>
              <li>
                <span></span>
                <span>
                  <input type="text" className="user_box"/>
                </span>
                <span></span>
                <span>
                  <input type="radio" name="userradio" />
                  
                  <input type="radio" name="userradio" />
                  
                </span>
              </li>
              <li>
                <span> </span>
                <span>
                  <input type="text"/>
                </span>
              </li>
              <li>
                <span></span>
                <span>
                  <input type="text"/>
                </span>
              </li>
            </ul>
          </div>
            <div className="user_top_button">
              <button onClick={handleClickShow}></button>
            </div>
          
          <div className="user_list_bottom">
            <h1> </h1>
            <div className="user_list_bottom_inner">
              <ul>
                <li>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                </li>
                {userList.map((item, index) => (
                  <li key={index}>
                    {/* onClick={e => onClickHandleUser} */}
                    <span>{item.user_id}</span>
                    <span>{item.name}</span>
                    <span>{item.birth_date}</span>
                    <span>{item.phone}</span>
                    <span>{item.gender === "0" ? (""):("")}</span>
                    <span>{item.role}</span>
                  </li>
                ))}
                
              </ul>
            </div>
          </div>
        </div>
      </div>
    </UserWrapper>
  );
};

export default UserManage;