import React, { useState } from "react";
import { useNavigate } from "react-router";
import { LoginMain } from "../css/login-style";
import { fetchLogin } from "../api/client";

const Login = () => {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [pass, setPass] = useState("");
  // const handletoMain = () => {
  //   navigate("/main");
  // };

  const handleLogin = async e => {
    console.log(id);
    console.log(pass);
    if (id === "" || pass === "") {
      alert("  !!");
    } else if (e.key === "enter") {
      alert("adawd");
    }
    try {
      e.preventDefault();
      const login = await fetchLogin(id, pass);
      setId("");
      setPass("");
    } catch (err) {
      console.log(err);
    } finally {
      navigate("/main");
    }
  };
  return (
    <LoginMain>
      <div className="login_inner">
        <div className="login_logo"></div>
        <h1 className="login_title">Camping Smore</h1>
        <form className="login_form">
          <span className="form_title"></span>
          <input
            type="text"
            placeholder=" "
            onChange={e => setId(e.target.value)}
          />
          <span className="form_title"></span>
          <input
            type="password"
            placeholder=" "
            onChange={e => setPass(e.target.value)}
          />
          <div className="help_account">
            <span>
                     .
            </span>
            <span>  </span>
            <div className="find_account">
              <span> </span>
              <span> </span>
            </div>
          </div>
        </form>
        <button
          // onClick={handletoMain}
          className="login_btn"
          onClick={handleLogin}
        >
          
        </button>
      </div>
    </LoginMain>
  );
};

export default Login;
