import React from "react";
import { ResponsivePieCanvas } from "@nivo/pie";

const MainChart = ({ manDataResult, womanDateResult, ageDataResult }) => {
  console.log(manDataResult, womanDateResult);

  const customColors = ["#06D98D", "#3366FF", "#FF7B01"]
  const genderData = [
    {
      id: "Man",
      label: "",
      value: parseInt(manDataResult),
    },
    {
      id: "Woman",
      labeld: "",
      value: parseInt(womanDateResult),
    },
  ];

  const ageData = [
    {
      id: "10",
      label: "0~10",
      value: ageDataResult[0]?.length || 0,
    },
    {
      id: "20",
      label: "10~20",
      value: ageDataResult[1]?.length || 0,
    },
    {
      id: "30",
      label: "20~30",
      value: ageDataResult[2]?.length || 0,
    },
    {
      id: "Woman",
      label: " ",
      value: ageDataResult[3]?.length || 0,
    },
  ];
  return (
    <div className="pie_wrapper">
      <ul className="pie_list">
        <li>
          <ResponsivePieCanvas
            data={genderData}
            keys={["value"]}
            indexBy="country"
            enableGridY={true}
            enableLabel={false}
            colors={customColors}
  
          />
          <p>  </p>
        </li>
        <li>
          <ResponsivePieCanvas
            data={ageData}
            keys={["value"]}
            indexBy="country"
            enableGridY={true}
            enableLabel={false}
            colors={customColors}
          />
          <p>  </p>
        </li>
      </ul>
    </div>
  );
};

export default MainChart;
