import axios from "axios";

//   
const getCategoryList = async () => {
  try {
    const res = await axios.get("/api/admin/item/category");
    const result = res.data;
    console.log("    ");
    console.log(result);
    return result;
  } catch (err) {
    console.log(err);
  }
  return [];
};

