import axios from "axios"

//    
const getUserList = async () => {
    try {
        const res = await axios.get("/api/admin/manage/users")
        const result = res.data;
        console.log("     ")
        console.log(result)
        return result;
    }catch (err){
        console.log(err)
    }
    return []
}
export default getUserList

